<template>
<div class="CalculateDegree padding students cur-poin pointAnalysis">
    <div class="breadcrumb-pd">
        <el-breadcrumb separator=">">
            <el-breadcrumb-item :to="{ path: '/Calculate/CalculateDegree' }">达成度计算</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/Calculate/CalculateDegree' }">毕业要求</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/Calculate/GraduationAch' }">毕业要求指标点</el-breadcrumb-item>
            <el-breadcrumb-item class="color_zhi">
                支撑课程
            </el-breadcrumb-item>
        </el-breadcrumb>
    </div>
    <div class="pointAnalysis1">
        <div class="pointAnalysis1_top">
            <div class="pointAnalysis1_left" @click="goback">
              <svgIcon icon-class="iconfanhui" size="14"></svgIcon>
                <span>返回上级</span>
            </div>
            <p v-if="nametitle">{{nametitle}}毕业要求达成度分析</p>
            <div class="pointAnalysis1_right">
              <svgIcon icon-class="icondaochu-excel" size="14"></svgIcon>
                <span>导出Excel</span>
            </div>
        </div>
        <div class="pointAnalysis1_botm">
            <img src="@/assets/img/dachengdu04.png" alt="" :style="{'width': fullWidth == 1920 ? '1131px' : '83%'}">
        </div>
    </div>
    <div class="echarts3" >
        <!-- 柱状图引用 -->
        <echarts3 :pointListcourses="pointList"></echarts3>
    </div>
    <div class="pointAnalysis_body1">
        
        <div class="PieCircle_class">
            <pinechars :pieCircleData = pieCircleData></pinechars>
        </div>
        <div class="pointAnalysis_body_table1">
            <el-table :data="tableData" stripe height="250" style="width: 100%" >
                <el-table-column prop="coursename" label="课程名称"  width="200">
                    <template slot-scope="scope">
                        <span style="margin-left: 10px; color:#03B187; font-size:14px;cursor: pointer;" @click="handdle(scope.row)">{{ scope.row.coursename }}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="examine" label="考核方式">
                </el-table-column>
                <el-table-column prop="analysis" label="指标点评价值" align="center" width="140">
                </el-table-column>
                <el-table-column prop="brace" label="支撑权重" align="center" width="140">
                </el-table-column>
                
                
            </el-table>
        </div>
    </div>
</div>
</template>

<script>
// import PieCircle from '../components/Echarts/PieCircle'
import echarts3 from "@/components/echaars/echars3"
import pinechars from "@/components/echaars/pinechars"
export default {
    name: 'PointAnalysis',
    components: {
       echarts3,
        pinechars
    },
    data() {
        return {
            nametitle:this.$route.query.nametitle || '',
            pointList: {},
            title: '',
            gradeOptions: [],
            classOptions: [],
            studentOptions: [],
            pieCircleData: [],
            seriesBarData: {},
            colorArr: ['#d87c7c', '#919e8b'],
            circleArr: ['#d87c7c', '#919e8b', '#d7ab82', '#6e7074', '#61a0a8'],
            tableData: [],
            form: {
                pointId: '',
                grade: '',
                classId: '',
                studentId: ''
            },
            api: {
                grades: '/basic/achieve/grades',
                allClass(scope) {
                    return `/basic/achieve/classes/${scope.grade}`
                },
                allStudent(scope) {
                    return `/basic/achieve/students/${scope.classId}`
                },
                getCourse: '/basic/achieve/analysis/course'
            },
            fullWidth:document.documentElement.clientWidth
        }
    },
    created() {
        this.form = this.$route.query
    },
    mounted() {
        this.grades()
        if (this.form.grade) {
            this.allClass()
        }
        if (this.form.classId) {
            this.allStudent()
        }
        this.getCourse()
    },
    methods: {
        goback(){
            this.$router.go(-1)
        },
        gradeChange() {
            this.allClass()
        },
        classChange() {
            this.allStudent()
        },
        handdle(row) {
            const queryObj = Object.assign(this.form, {
                courseId: row.courseId,
                coursename:row.coursename,
                nametitle:this.$route.query.nametitle || '',
            })
            this.$router.push({
                path: '/Calculate/courseAnalysis',
                query: queryObj
            })
        },
        //年级查询
        grades() {
            this.$get(this.api.grades).then(res => {
                if (res.status === 1) {
                    this.gradeOptions = res.data
                }
            })
        },
        //班级查询
        allClass() {
            const url = this.api.allClass({
                grade: this.form.grade
            })
            this.$get(url).then(res => {
                if (res.status === 1) {
                    this.classOptions = res.data
                }
            })
        },
        //学生查询
        allStudent() {
            const url = this.api.allStudent({
                classId: this.form.classId
            })
            this.$get(url).then(res => {
                if (res.status === 1) {
                    this.studentOptions = res.data
                }
            })
        },
        getCourse() {
            this.$post(this.api.getCourse, this.form).then(res => {
                if (res.status === 1) {
                    this.title = res.data.title
                    //表格
                    this.tableData = res.data.points
                    //柱状图
                    const seriesBarObj = {
                        pointcode:this.$route.query.pointcode,
                        pointname:this.$route.query.pointname,
                        legendData: res.data.sign,
                        xAxisData: res.data.coursename,
                        seriesData: [{
                                name: res.data.target.name,
                                barGap: "5%",
                                data: res.data.target.value || [],
                                markPoint: {
                                    data: [{
                                            type: 'max',
                                            name: '最大值'
                                        },
                                        {
                                            type: 'min',
                                            name: '最小值'
                                        }
                                    ]
                                }
                            },
                            {
                                name: res.data.analysis.name,
                                barGap: "5%",
                                data: res.data.analysis.value || []
                            }
                        ]
                    }
                    this.pointList = seriesBarObj
                    // this.seriesBarData = seriesBarObj
                    //饼图
                    res.data.cake.forEach(element => {
                        element.pointcode=this.$route.query.pointcode
                    });
                    this.pieCircleData = res.data.cake
                } else {
                    this.$notify.error({
                        title: 'error',
                        duration: 1500,
                        message: res.message
                    })
                }
            })
        },
        calculate() {
            this.getCourse()
        },
    }
}
</script>

<style lang="scss" >
.pointAnalysis {
    min-width: 1021px;
     overflow: hidden;
    margin-bottom: 50px;
    .breadcrumb-pd{
        padding: 33px 0px ;
        .el-breadcrumb__item:last-child .el-breadcrumb__inner{
            color: #03B187;
        }
        .el-breadcrumb__inner.is-link, .el-breadcrumb__inner a{
            color: #696969;
        }
    }
    .pointAnalysis1{
        box-sizing: border-box;
        width: 100%;
        height: 196px;
        background: url("../../assets/img/bg-chaxun.png") no-repeat;
        background-size: 100%;
        margin-bottom: 20px;
        .pointAnalysis1_top{
            padding: 28px 0 1vw 1vw ;
            padding-right: 1vw;
            display: flex;
            .pointAnalysis1_left{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                cursor: pointer;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
            p{  
                flex:1;
                text-align: center;
                font-size:24px;
                font-family:PingFang SC;
                font-weight:bold;
                color:rgba(255,255,255,1);
                // margin-left: 10vw;
            }
            .pointAnalysis1_right{
                flex:0.1;
                width:120px;
                height:28px;
                border:1px solid rgba(176,249,224,1);
                border-radius:14px;
                padding-left: 5px;
                padding-right: 5px;
                text-align: center;
                img{
                    width: 12%;
                }
                span{
                    font-size:14px;
                    font-family:PingFang SC;
                    font-weight:500;
                    color:rgba(176,249,224,1);
                    line-height:30px;
                }
            }
        }
        .pointAnalysis1_botm{
            margin-top: 22px;
            // padding-left:5vw;
            // padding-right: 5vw;
            img{
                margin: 0 auto;
                display: flex;
                // width: 100%;
            }
        }
    }
    .echarts3{
        background-color: #fff;
        padding-top: 25px;
    }
    .pointAnalysis_header {
        overflow: hidden;
        position: relative;
        margin: 40px 0 45px 0;

        .margin-r {
            margin-right: 20px;
        }

        .float-l {
            float: left;
        }

        .calculate-btn {
            position: absolute;
            right: 0;
            top: -20%;
        }
    }

    .pointAnalysis_body_echarts {
        width: 95%;
        float: left;
        border: 1px solid rgb(220, 223, 230);
        padding-top: 18px;
        margin: 0 0 30px 2.5%;
        #pieCircle {
            float: left;
        }

    }

    .pointAnalysis_body1 {
        margin-top: 18px;
        display: flex;
        margin-bottom: 50px;
        .pointAnalysis_body_title {
            margin-bottom: 40px;
            padding: 25px 35px;

            span:nth-child(1) {
                float: left;
                font-weight: 700;
                font-size: 16px;
            }

            span:nth-child(2) {
                float: right;
                border: 1px solid #DCDFE6;
                font-size: 14px;
                padding: 5px 8px;
            }
        }
        .PieCircle_class{
            background-color: #fff;
            flex:1;
            padding-top: 25px;
            padding-bottom: 25px;
            border-radius:10px;
            padding-left: 30px;
            padding-right: 30px;
        }
        .pointAnalysis_body_table1 {
            border-radius:10px;
            width: 45vw;
            padding-bottom: 5px; 
            float: left;
            background-color: #fff;
            margin-left: 20px;
            padding-top: 0;
            flex: 1.5;
        }
    }
}
</style>



